import styled from 'styled-components';

const MAIN_COLOR = '#134984';
const MAIN_BG_COLOR = '#B9CCD8';
const CONTENT_BG_COLOR = '#E0E8ED';
const SHADOW_COLOR = '#ccc'
const DIALOG_COLOR = '#00595a'

export const LinkBack = styled.a`
  color: ${MAIN_COLOR};
  position: fixed;
  top: 8px;
  left: 6px;
  width: 28px;
  height: 28px;
  text-decoration: none;
  font-size: 1em;
  line-height: 28px;
  text-align: center;
  border-radius: 16px;
  background: url('./left.png') 2px center  white no-repeat;
  background-size: 80%;
  z-index: 200;
  font-family: arial;
  border: 2px solid ${MAIN_COLOR};
`;

export const HF = styled.div`
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-shadow: 1px 1px 2px white, 0 0 1em white, 0 0 0.2em white;
`;

export const AdventQuote = styled.h1`
  background: ${MAIN_COLOR};
  color: white;
  height: 50px;
  font-size: 1em;
  text-align: center;
  line-height: 50px;
  font-weight: 100;
  font-style: italic;
`;

export const ContentImage = styled.div`
  width: calc(100% - 20px);
  border: 10px solid white;
  height: 200px;
  background-image: url('./pics/2025/${props => props['data-img']}');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 1px 1px 2px ${SHADOW_COLOR}, 0 0 1em ${SHADOW_COLOR}, 0 0 0.2em ${SHADOW_COLOR};
`;

export const AdventTop = styled(HF)`
  width: 100%;
  position: relative;
  min-height: 120px;
  background-color: ${MAIN_BG_COLOR};
  background-image: url('./bgbottom.png');
  background-size: cover;
  background-position: bottom right;
  background-repeat: no-repeat;
  padding: 30px 0;
  h2 {
    padding: 0 50px;
    text-align: center;
  }
`;

export const HiddenLink = styled.a`
  display: block;
  width: 40px;
  height: 40px;
  bottom: 0;
  right: 0;
  position: absolute;
  background-color: red;
`;

export const Datum = styled.p`
  font-size: 12px;
  margin-bottom: 20px;
`;

export const Advent = styled.div`
  width: 100%;
  background-size: 100%;
  background-image: url('./bg.png');
`;

export const Content = styled.div`
  margin: 0;
  padding: 20px 40px;
  background: ${CONTENT_BG_COLOR};  
  p {
    padding: 15px 0;
    font-size: 1em;
    line-height: 1.5em;
    span {
      display: inline-block;
      font-style: italic;
      color: ${DIALOG_COLOR};
      margin: 5px 0;
    }
  }
  hr {
    height: 0;
    border-top: 1px solid ${MAIN_COLOR};
    border-bottom: 1px solid white;
  }
  blockquote {
    margin: 0;
    padding: 10px 30px 10px 50px;
    color: ${DIALOG_COLOR};
    background: url('./quote.png') top left no-repeat;
    
  }
  ul {
    margin: 0;
    padding: 0 0 0 20px;
  }
  li {
    margin: 0;
    padding: 10px 0;
  }
`;

export const ContentMain = styled.div`
  margin: 0;
  padding: 10px 0;
  background: rgba(255,255,255,.5);  
`;

export const AdventBottom = styled(HF)`
  width: 100%;
  min-height: 90px;
  background-color: ${MAIN_BG_COLOR};
  background-image: url('./bgtop.png'); 
  background-size: cover;
  background-position: top left;
  background-repeat: no-repeat;
  h3 {
    padding: 20px 30px;
    text-align: center;
    line-height: 1.5em;
    span {
      display: block;
      padding-top: 10px;
      font-weight: 400;
      text-align: right;
      font-size: .625em;
    }
  }
`;

export const Days = styled.ol`
  list-style: none;
  margin: 20px 0 0 0;
  padding: 0 0 0 4%;
`;

export const DaysEmpty = styled.div`
  color: ${MAIN_COLOR};
  min-height: 400px;
  line-height: 400px;
  margin: 0 30px;
  text-align: center;
  vertical-align: middle;
`;

export const Day = styled.li`
  margin: 0;
  display: inline-block;
  width: 21%;
  padding: 0;
  margin: 0 2% 3% 1%; 
  a, span {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;    
    height: ${props => parseInt(props['data-w'] > 600 ? 120 : props['data-w']*.20,10)}px;
    width: 100%;
    color: ${MAIN_COLOR};
    font-size: 1.5em;
    background-image: url('./pics/${props => props['data-nr']}.jpg');
    background-size: contain;
    border-radius: 10px;
    &:before {
      border-radius: 10px;
      content: " ";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(217,229,236,0.3);
    }
  }
  .today {
    &:before {
      top: -4px;
      bottom: -4px;
      left: -4px;
      right: -4px;
      border: 4px solid rgba(19,73,132,.5);
      background: rgba(217,229,236,0);
    }
  }
  span {
    &:before {
      background: rgba(217,229,236,.8);
    }
  }
`;
