import React, { useState, useEffect } from 'react';
import { useLocation, HashRouter as Router, Route, Routes, useParams } from 'react-router-dom';
import {
  LinkBack, HiddenLink, AdventQuote, ContentImage, AdventTop, Advent, Content, ContentMain, AdventBottom, Days, DaysEmpty, Day
} from './Styles'
import { Parser } from "html-to-react";
import SRC from './2025.json';

const useW = () => {
  const [w, setW] = useState(undefined);
  const hasWindow = typeof window !== 'undefined';
  
  useEffect(() => {
    const root = document.getElementById('root');

    function calculateWidth() {
      return window.innerWidth > 600 ? 600 : root.getBoundingClientRect().width;
    }
    function handleResize() {
      setW(calculateWidth());
      document.getElementById('root').style.width = window.innerWidth > 600 ? '600px' : `${window.innerWidth}px`;
    }
    window.addEventListener("resize", handleResize);
    handleResize();
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return hasWindow ? w : null;
}

const Home = () => {
  const myOrder = [
    0, 1, 2, 3,
    2, 3, 0, 1,
    1, 2, 3, 0,
    3, 0, 1, 2,
    2, 3, 0, 1,
    0, 1, 2, 3
  ];

  const [mo, setMo] = useState(11);
  
  const isDayNotYet = (d, idx) => d.getMonth()=== mo && d.getDate()>idx;
  const isDayToday = (d,idx) => d.getMonth()=== mo && d.getDate()===idx+1;
  const w = useW();
  const list = Object.keys(SRC);
  
  const toggleMo = () => {
    if(mo === 11) {
      setMo(10)
    } else {
      setMo(11)
    }
  }

  const returnKeys = () => {
    const d = new Date();
    if(d.getMonth() !== mo) {
      return <DaysEmpty>Nemsokra...</DaysEmpty>
    }

    return <Days>{list.map((key, idx) => {
      const className = isDayToday(d, idx) ? 'today' : undefined;
      return (
        <Day data-w={w} data-nr={myOrder[idx]} key={key}>
          {isDayNotYet(d, idx) ? <a href={`#/${key}`} className={className}>{idx+1}</a> : <span>{idx+1}</span>}
        </Day>
      )
    })}</Days>
  }

  return (
    <>
      <Advent>
        <AdventTop>
          <h1>Advent 2025</h1>
          {<HiddenLink onClick={toggleMo} />}
        </AdventTop>
      
        <ContentMain>
         {returnKeys()}    
        </ContentMain>
      
        <AdventBottom>
          <h3>Boldog Adventet</h3>
        </AdventBottom>
      </Advent>
    </>
  );
}



function AdventPage() {
  const Def = (props) => {
    console.log(props);
    return <>{props.children}</>
  }
  const [Datum, setDatum] = useState(Def);
  const { param } = useParams();
  const obj = SRC[param];

  import("./Styles").then((P) => {
    setDatum(P.Datum);
  })
  const htmlParser = new Parser();

  if(!obj) {
    return <></>
  }

  return (
    <>
      <Advent>
        <LinkBack href="#/" />
        <AdventQuote>
          Egy trtnet...
        </AdventQuote>
        <AdventTop>
          <Datum>{obj.date}</Datum>
          <h2>{htmlParser.parse(obj.title)}</h2>
        </AdventTop>
      
        <Content>
          {!!obj.image && <ContentImage data-img={obj.image} /> }
          {htmlParser.parse(obj.story)}
        </Content>
        <AdventQuote>
          ...s egy idzet
        </AdventQuote>
        <AdventBottom>
          <h3>{htmlParser.parse(obj.quote)}</h3>
        </AdventBottom>
      </Advent>
    </>
  )
}

function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  return (
    <Router future={{ v7_startTransition: true }}>
        <ScrollToTop />
        <Routes>
          <Route path="/:param" element={<AdventPage />} />
          <Route path="/" element={<Home />} />
        </Routes>
    </Router>
  );
}

export default App;